import { FETCHED_FRIENDS, UPDATE_FRIENDS } from './actionType';
import {getFriends} from '../../utils/api/vk';

export const fetchedFriends = () => async (dispatch) => {
	const data = await getFriends();
	dispatch({type: FETCHED_FRIENDS, users: data});
};

export const updateFriends = (setFetching) => async (dispatch) => {
	const data1 = await getFriends(100, 100);
	const data2 = await getFriends(100, 200);
	debugger
	setFetching()
	dispatch({type: UPDATE_FRIENDS, users: [...data1, ...data2]});
};